<template>
    <div class="location">
        {{location.name}} - <span v-on:click="edit">{{item}}</span>

        <ItemList v-if="editing" @item-select="selectItem"/>
    </div>
</template>
<script>
    import ItemList from "@/components/ItemList"
    import axios from "axios"

    export default {
        name: 'Location',
        components: {ItemList},
        props: {
            location: {},
        },
        data: function() {
          return {
              editing: false,
              item: this.location.item
          }
        },
        methods: {
            edit: function() {
                this.editing = true
            },
            selectItem: function(item) {
                axios
                    .put(`https://htun05wn1f.execute-api.us-east-1.amazonaws.com/prod/locations/${encodeURIComponent(this.location.name)}`,
                        { item: item.name })
                    .then(response => {
                        this.item = item.name
                        this.editing = false
                    })
            }
        }
    }
</script>
<style scoped>

    img {
        border-radius: 30pt;
        max-width: 500px;
    }
</style>