<template>
    <img v-bind:src="src" width="300px"/>
</template>
<script>
    export default {
        name: 'ItemImage',
        props: {
            item: {}
        },
        computed: {
            src: function () {
                return `https://semservice.s3.amazonaws.com/${encodeURIComponent(this.item.name)}.jpeg`
            },
        }
    }
</script>
<style scoped>

    img {
        border-radius: 30pt;
        max-width: 500px;
    }
</style>