<template>
  <ul id="stock">
    <li v-for="item in items" :key="item.name">
      <BuyItem :item="item"/>
    </li>
  </ul>
</template>

<script>
  import BuyItem from "@/components/BuyItem";

  import axios from "axios"

export default {
  name: 'Stock',
  components: {BuyItem},
  props: {
    // msg: String,
    link: String
  },
  data: function() {
    return {
      items: null
    };
  },
  mounted () {
    axios
      .get('https://htun05wn1f.execute-api.us-east-1.amazonaws.com/prod/stock')
      .then(response => (this.items = response.data.list))
  }
}
</script>

