<template>
    <a v-bind:href="buyLink"><Item :item="item"/></a>
</template>
<script>
    import Item from "@/components/Item";

    export default {
        name: 'BuyItem',
        components: {Item},
        props: {
            item: {}
        },
        computed: {
            buyLink: function () {
                return `https://bunq.me/SemService/${encodeURIComponent(this.item.price)}/${encodeURIComponent(this.item.name)}`
            },
        }
    }
</script>
<style scoped>

    a {
        text-decoration: none;
        font-size: 50px;
        color: white;
    }
</style>