<template>
  <ul id="stock">
    <li v-for="location in locations" :key="location.name">
      <Location :location="location"/>
    </li>
  </ul>
</template>

<script>
  import Location from "@/components/Location.vue";

  import axios from "axios"

export default {
  name: 'LocationList',
  components: {Location},
  props: {
    // msg: String,
    link: String
  },
  data: function() {
    return {
      locations: null
    };
  },
  mounted () {
    axios
      .get('https://htun05wn1f.execute-api.us-east-1.amazonaws.com/prod/locations')
      .then(response => (this.locations = response.data.list))
  }
}
</script>

