<template>
  <ul id="stock">
    <li v-for="item in items" :key="item.name">
      <Item :item="item" @item-click="$emit('item-select', item)"/>
    </li>
  </ul>
</template>

<script>
  import Item from "@/components/Item.vue";

  import axios from "axios"

export default {
  name: 'ItemList',
  components: {Item},
  props: {
    // msg: String,
    link: String
  },
  data: function() {
    return {
      items: null
    };
  },
  mounted () {
    axios
      .get('https://htun05wn1f.execute-api.us-east-1.amazonaws.com/prod/items')
      .then(response => (this.items = response.data.list))
  }
}
</script>

